<template>
  <div class="systems--parent-component">
    <div v-if="isLoading" class="md-layout md-alignment-top-center">
      <md-progress-spinner
        md-mode="indeterminate"
        :md-diameter="150"
        :md-stroke="10"
      ></md-progress-spinner>
    </div>
    <systems-list v-if="!isLoading" :systems="computedSystems">
      <template slot-scope="system">
        <md-card-media>
          <img :src="system.idAttachmentCoverURL" />
        </md-card-media>
        <md-card-header>
          <div class="md-title">{{ system.name | getName }}</div>
        </md-card-header>
        <md-card-expand>
          <md-card-actions md-alignment="space-between">
            <div>
              <md-button class="md-primary" v-on:click="buynow(system.name)"
                >BUY NOW</md-button
              >
            </div>

            <md-card-expand-trigger>
              <md-button v-if="system.desc">Learn more</md-button>
            </md-card-expand-trigger>
          </md-card-actions>

          <md-card-expand-content>
            <md-card-content v-html="system.desc"></md-card-content>
          </md-card-expand-content>
        </md-card-expand>
        <br />
      </template>
    </systems-list>
  </div>
</template>

<script>
import SystemsList from "./SystemsList"
import { RepositoryFactory } from "./../repositories/RepositoryFactory"

const SystemsRepository = RepositoryFactory.get("systems")

var PUBLISHABLE_KEY = "pk_test_PO7L36i5OKpTpYFK8zUh1pMl00SD6RSByQ"
var DOMAIN = "https://mysterybyte.github.io"

/* global Stripe */
var stripe = Stripe(PUBLISHABLE_KEY)

export default {
  name: "parent-component",
  components: { SystemsList },
  data() {
    return {
      isLoading: false,
      systems: []
    }
  },
  created() {
    this.fetch()
  },
  methods: {
    async fetch() {
      this.isLoading = true
      const { data } = await SystemsRepository.getCards()

      let computedSystems = []
      for (let i = 0; i < data.length; i++) {
        let system = data[i]
        let idAttachmentCoverURL = ""
        if (system.idAttachmentCover) {
          const { data } = await SystemsRepository.getCardAttachment(
            system.id,
            system.idAttachmentCover
          )
          idAttachmentCoverURL = data.url
        }

        computedSystems[i] = {
          id: system.id,
          idAttachmentCover: system.idAttachmentCover,
          idAttachmentCoverURL: idAttachmentCoverURL,
          name: system.name,
          desc: system.desc
        }
      }
      this.isLoading = false
      this.systems = computedSystems
    },
    buynow: function(SKU_ID) {
      SKU_ID = SKU_ID.split("||")[0]
      // eslint-disable-next-line
      console.log(SKU_ID)
      stripe
        .redirectToCheckout({
          items: [{ sku: SKU_ID, quantity: 1 }],
          successUrl: DOMAIN + "/",
          cancelUrl: DOMAIN + "/"
        })
        .then(function(result) {
          // eslint-disable-next-line
          console.log(result.error.message)
        })
    }
  },
  computed: {
    computedSystems() {
      return this.systems
    }
  },
  filters: {
    getName: function(value) {
      return value.split("||")[1]
    }
  }
}
</script>

<style lang="scss" scoped>
img {
  opacity: 0.3;
  filter: alpha(opacity=30); /* For IE8 and earlier */
}
.md-card:hover {
  img {
    opacity: 1;
    filter: alpha(opacity=100); /* For IE8 and earlier */
  }
}
.md-progress-spinner {
  margin: 5em 0;
}
</style>
