import Repository from "./Trello"

//const boardId = "5dcefeb54b0c6a5c2b4508e0"
const listId = "5dcefeb59352dd7aae7dd2eb"

export default {
  getCards() {
    // eslint-disable-next-line
    //console.log(Repository.get(`lists/${listId}/cards`))
    return Repository.get(`lists/${listId}/cards`)
  },
  getCardAttachment(cardId, idAttachmentCover) {
    return Repository.get(`cards/${cardId}/attachments/${idAttachmentCover}`)
  }
}
